<template>
  <div>

    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/mainTwo'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>房产管理</el-breadcrumb-item>
      <el-breadcrumb-item>房屋管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">

      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入房间号" v-model="number">
            <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true">添加房间</el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData">
        <el-table-column prop="id" label="序号" width="90"></el-table-column>
        <el-table-column prop="number" label="房间号" width="110"></el-table-column>
        <el-table-column prop="rnumber" label="单元号" width="110"></el-table-column>
        <el-table-column prop="area" label="面积" width="110"></el-table-column>
        <el-table-column prop="type" label="类型" width="110"></el-table-column>
        <el-table-column label="操作" min-width="150">
          <template slot-scope="scope">
            <el-button type="primary" @click="showReadDialog(scope.row.id)" icon="el-icon-reading">查看</el-button>
            <el-button type="warning" @click="showEditDialog(scope.row.id)" icon="el-icon-edit">修改</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeUserById(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total,prev, pager, next"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog
      title="添加房屋"
      :visible.sync="addDialogVisible"
      width="40%"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px" @close="addDialogClosed">
        <el-form-item label="房间号" prop="number">
          <el-input v-model="addForm.number"></el-input>
        </el-form-item>
        <el-form-item label="单元号" prop="rnumber">
          <el-input v-model="addForm.rnumber"></el-input>
        </el-form-item>
        <el-form-item label="面积" prop="area">
          <el-input v-model="addForm.area"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-input v-model="addForm.type"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRoom">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改房屋"
      :visible.sync="editDialogVisible"
      width="40%"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px" @close="editDialogClosed">
        <el-form-item label="房间号" prop="number">
          <el-input v-model="editForm.number"></el-input>
        </el-form-item>
        <el-form-item label="单元号" prop="rnumber">
          <el-input v-model="editForm.rnumber"></el-input>
        </el-form-item>
        <el-form-item label="面积" prop="area">
          <el-input v-model="editForm.area"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-input v-model="editForm.type"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editRoom">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="查看"
      :visible.sync="readDialogVisible"
      width="40%"
    >
      <el-form :model="readForm"  ref="readForm" label-width="70px" >
        <el-form-item label="房间号" prop="number">
          <el-input v-model="readForm.number" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="单元号" prop="rnumber">
          <el-input v-model="readForm.rnumber" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="面积" prop="area">
          <el-input v-model="readForm.area" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-input v-model="readForm.type" :disabled="true"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="readDialogVisible = false">关  闭</el-button>
      </span>
    </el-dialog>


  </div>
</template>

<script>
  export default {
    name: "roomManageTwo",
    data() {
      return {
        pageSize: 8,
        currentPage: 1,
        tableData: [],
        total: 0,
        number: "",
        readDialogVisible:false,
        readForm:{
          number:"",
          rnumber:"",
          area:"",
          type:""
        },
        addDialogVisible: false,
        addForm:{
          number:"",
          rnumber:"",
          area:"",
          type:""
        },
        addFormRules:{
          number:[
            {required: true, message: '房间号不可为空', trigger: 'blur'},
            {min:3,max:20,message: '房间号长度在2~20个字符之间',trigger:'blur'}
          ],
          rnumber:[
            {required: true, message: '单元号不可为空', trigger: 'blur'}
          ],
          area:[
            {required: true, message: '面积不可为空', trigger: 'blur'}
          ],
          type:[
            {required: true, message: '户型不可为空', trigger: 'blur'}
          ]
        },
        editDialogVisible:false,
        editForm:{
          id:"",
          number:"",
          rnumber:"",
          area:"",
          type:""
        },
        editFormRules:{
          number:[
            {required: true, message: '房间号不可为空', trigger: 'blur'},
            {min:3,max:20,message: '房间号长度在2~20个字符之间',trigger:'blur'}
          ],
          rnumber:[
            {required: true, message: '单元号不可为空', trigger: 'blur'}
          ],
          area:[
            {required: true, message: '面积不可为空', trigger: 'blur'}
          ],
          type:[
            {required: true, message: '户型不可为空', trigger: 'blur'}
          ]
        }
      }
    },
    created() {
      this.queryRoomList()
    },
    methods: {
      queryRoomList() {
        this.$http.get('room/queryRoom',
          {params: {currentPage: this.currentPage, pageSize: this.pageSize, number: this.number}})
          .then(res => {
            this.total = res.data.total;
            this.tableData = res.data.data;
            //this.tableData=res.data;
          });
      },
      handleCurrentChange(currentPage) {
        this.currentPage = currentPage
        this.queryRoomList()
      },
      handleSearch() {
        this.currentPage = 1
        this.queryRoomList()
      },
      addDialogClosed(){
        this.$refs.addFormRef.resetFields()
      },
      addRoom(){
        this.$refs.addFormRef.validate(async valid=>{
          if(!valid) return
          const {data:res}=await this.$http.post("room/addRoom",this.addForm)
          if(res.code!==200){
            this.$message.error("添加房间信息失败,请检查房间信息")
          }else {
            this.$message.success("添加房间信息成功")
            this.addDialogVisible=false
            this.queryRoomList()
          }
        })
      },
      showEditDialog(id){
        this.editDialogVisible =true
        this.$http.get('room/getRoomById',{params:{id:id}})
          .then(res => {
            this.editForm = res.data;
            //this.tableData=res.data;
          });
      },
      editDialogClosed(){
        this.$refs.editFormRef.resetFields()
      },
      editRoom(){
        this.$refs.editFormRef.validate(async valid =>{
          if(!valid) return
          const {data:res}=await this.$http.post("room/editRoom",this.editForm)
          if(res.code!==200){
            this.$message.error("修改房间信息失败,请检查房间信息")
          }else {
            this.$message.success("修改房间信息成功")
            this.editDialogVisible=false
            this.queryRoomList()
          }
        })
      },
      removeUserById(id){
        this.$confirm('此操作将永久删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.get('room/removeRoomById',{params:{id:id}});
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.queryRoomList();
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      showReadDialog(id){
        this.readDialogVisible =true
        this.$http.get('room/getRoomById',{params:{id:id}})
          .then(res => {
            this.readForm = res.data;
            //this.tableData=res.data;
          });
      },
      // findRoomList(){
      //   this.currentPage = 1
      //   this.$http.get('room/findRoom',
      //     {params:{currentPage:1,pageSize:this.pageSize,number:this.number}})
      //     .then(res=>{
      //       this.total=res.data.total;
      //       this.tableData=res.data.data;
      //       //this.tableData=res.data;
      //     });
      // }
    }
  }
</script>

<style scoped>
  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 18px;
  }

  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  }
</style>



